import React, { useState, useEffect, useMemo } from "react";
import Select, { components } from "react-select";

interface Option {
  value: string;
  label: string;
  flag: string;
}

interface LoginProps {
  appSettings: {
    AuthenticationUrls: Record<string, string>;
    AgencyFlags: Record<string, string>;
  };
  encrypt: (value: string) => string;
  showLoggedInUI: (username: string) => void;
}

const Login: React.FC<LoginProps> = ({ appSettings, encrypt, showLoggedInUI }) => {
  const [selectedAgency, setSelectedAgency] = useState<Option | null>(null);
  const [username, setUsername] = useState<string>("");
  const [password, setPassword] = useState<string>("");
  const [error, setError] = useState<string>("");

  // Memoize the options array to prevent unnecessary re-renders
  const options: Option[] = useMemo(
    () =>
      Object.entries(appSettings.AuthenticationUrls).map(([agency, url]) => ({
        value: url,
        label: agency,
        flag: appSettings.AgencyFlags[agency],
      })),
    [appSettings]
  );

  useEffect(() => {
    if (options.length > 0) {
      setSelectedAgency(options[0]); // Set the first agency as default
    }
  }, [options]);

  const handleLogin = async () => {
    if (!selectedAgency || !username || !password) {
      setError("Please fill in all fields.");
      return;
    }

    console.log("Selected Agency:", selectedAgency);

    const url = selectedAgency.value; // Use the value of the selected agency
    if (!url) {
      setError("Invalid agency selected.");
      return;
    }

    const payload = {
      userName: username,
      password: password,
      loginResponse: {},
    };

    try {
      const response = await fetch(url, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(payload),
      });

      const result = await response.json();
      const accessToken = result?.token?.access_token;
      const refreshToken = result?.token?.refresh_token;

      if (!accessToken || !refreshToken) {
        setError("Login failed. Please check your credentials.");
        return;
      }

      const settings = Office.context?.roamingSettings;
      if (settings) {
        settings.set("access_token", encrypt(accessToken));
        settings.set("refresh_token", encrypt(refreshToken));
        settings.set("erp_username", result?.userName || username);
        settings.saveAsync(() => {
          showLoggedInUI(result?.userName || username);
        });
      }
    } catch (err) {
      setError("An error occurred during login. Please try again.");
    }
  };

  // Custom rendering for dropdown options
  const customSingleValue = (props: any) => (
    <components.SingleValue {...props}>
      <div style={{ display: "flex", alignItems: "center" }}>
        <img
          src={props.data.flag}
          alt={props.data.label}
          style={{ width: "20px", height: "15px", marginRight: "10px" }}
        />
        {props.data.label}
      </div>
    </components.SingleValue>
  );

  const customOption = (props: any) => (
    <components.Option {...props}>
      <div style={{ display: "flex", alignItems: "center" }}>
        <img
          src={props.data.flag}
          alt={props.data.label}
          style={{ width: "20px", height: "15px", marginRight: "10px" }}
        />
        {props.data.label}
      </div>
    </components.Option>
  );

  return (
    <div style={{ maxWidth: "400px", margin: "0 auto", padding: "20px", border: "1px solid #ccc", borderRadius: "8px" }}>
      <h2>Login</h2>
      <div>
        <label htmlFor="agency-select">Select Agency:</label>
        <Select
          id="agency-select"
          value={selectedAgency} // Bind the selectedAgency state to the value prop
          onChange={(option) => setSelectedAgency(option as Option)} // Update the state with the selected option
          options={options}
          components={{ SingleValue: customSingleValue, Option: customOption }}
        />
      </div>
      <div>
        <label htmlFor="username">User Name:</label>
        <input
          id="username"
          type="text"
          value={username}
          onChange={(e) => setUsername(e.target.value)}
          style={{ width: "100%", marginBottom: "10px" }}
        />
      </div>
      <div>
        <label htmlFor="password">Password:</label>
        <input
          id="password"
          type="password"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          style={{ width: "100%", marginBottom: "10px" }}
        />
      </div>
      {error && <p style={{ color: "red" }}>{error}</p>}
      <button onClick={handleLogin} style={{ width: "100%", padding: "10px", backgroundColor: "#0078d4", color: "#fff", border: "none", borderRadius: "4px" }}>
        Log In
      </button>
    </div>
  );
};

export default Login;